import { Plugin } from '@ali/4ever-cangjie';
import { unselectSubject } from '../commands';
import { hasSubjectBeenSelected } from '../helpers';

function createOnCangjieInput(): Plugin['onCangjieInput'] {
  return function onCangjieInput(event, controller, next) {
    if (hasSubjectBeenSelected(controller)) {
      controller.command(unselectSubject);
    }
    return next();
  };
}

export default createOnCangjieInput;
